<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 신청 내역</h2>
      <div class="d-flex justify-content-end align-items-center mb-3 search-controls">
        <!-- 날짜와 이름 그룹을 한 줄로 병합 -->
        <div class="d-flex align-items-center">
          <!-- 날짜 그룹 -->
          <label for="dateSelect" class="mr-2">날 짜 :</label>
          <select class="form-control w-auto mr-3" id="dateSelect" v-model="selectedDate">
            <option v-for="date in dates" :key="date" :value="date">{{ date }}</option>
          </select>

          <!-- 이름 그룹 -->
          <label for="nameSearch" class="mr-2">이 름 :</label>
          <input type="text" class="form-control w-auto mr-3 name-input" id="nameSearch" v-model="searchName" placeholder="이름 검색">

          <!-- 조회 버튼 -->
          <button class="btn btn-primary search-button" @click="searchData">조회</button>
        </div>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>이름</th>
            <th>메일</th>
            <th>건수</th>
            <th>신청금액</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in paginatedData" :key="item.email">
            <td>{{ item.name }}</td>
            <td>{{ item.email }}</td>
            <td>{{ item.count }}</td>
            <td>{{ item.requested.toLocaleString() }}원</td>
          </tr>
        </tbody>
      </table>

      <!-- 페이징 컨트롤 -->
      <nav>
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <a class="page-link page-button" href="#" @click.prevent="changePage(currentPage - 1)">Previous</a>
          </li>
          <li v-for="page in totalPages" :key="page" class="page-item" :class="{ active: page === currentPage }">
            <a class="page-link page-button" href="#" @click.prevent="changePage(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <a class="page-link page-button" href="#" @click.prevent="changePage(currentPage + 1)">Next</a>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>


<script setup>
import { ref, computed, onMounted } from 'vue';

const selectedDate = ref('2024-01');
const searchName = ref('');
const data = ref([]);
const currentPage = ref(1);
const rowsPerPage = 10;
const dates = ref(['2024-01', '2024-02', '2024-03']); // 날짜 목록

// 필터링된 데이터 및 페이지당 데이터 계산
const filteredData = computed(() => {
  return data.value.filter(item =>
    item.name.toLowerCase().includes(searchName.value.toLowerCase())
  );
});

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage;
  return filteredData.value.slice(start, start + rowsPerPage);
});

const totalPages = computed(() => {
  return Math.ceil(filteredData.value.length / rowsPerPage);
});

// 데이터 요청 및 처리
const fetchData = async () => {
  try {
    // 예시 데이터 (실제 API 호출 필요 시 수정)
    data.value = [
      { name: '홍길동', email: 'hong@example.com', count: 3, requested: 1000000 },
      { name: '김철수', email: 'kim@example.com', count: 5, requested: 1500000 },
      { name: '이영희', email: 'lee@example.com', count: 2, requested: 800000 },
    ];
  } catch (error) {
    console.error('Error fetching data:', error);
  }
};

const searchData = () => {
  currentPage.value = 1; // 검색 시 첫 페이지로 이동
};

const changePage = (page) => {
  if (page > 0 && page <= totalPages.value) {
    currentPage.value = page;
  }
};

// 컴포넌트가 마운트될 때 데이터 가져오기
onMounted(() => {
  fetchData();
});
</script>

