// src/router/receipt.js
import ReceiptAdminPersonalRequestDetails from '@/views/receipt/ReceiptAdminPersonalRequestDetails.vue';
import ReceiptAdminSubmission from '@/views/receipt/ReceiptAdminSubmission.vue';
import ReceiptAdminMonthlyDetails from '@/views/receipt/ReceiptAdminMonthlyDetails.vue';
import ReceiptAdminClosure from '@/views/receipt/ReceiptAdminClosure.vue';
import ReceiptUserPersonalRequestDetails from '@/views/receipt/ReceiptUserPersonalRequestDetails.vue';
import ReceiptUserRegistrationMain from '@/views/receipt/ReceiptUserRegistrationMain.vue';
import ReceiptUserSettlementList from '@/views/receipt/ReceiptUserSettlementList.vue';

const vueTest = true;

const receiptRoutes = [

  
  
  // --------------------- 사용자 영역 ---------------------

  {
    // 영수증 등록
    path: 'user/registration-main',
    name: 'ReceiptUserRegistrationMain',
    component: ReceiptUserRegistrationMain,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },
  {
    // 영수증 상세 내용
    path: 'user/personal-request-details',
    name: 'ReceiptUserPersonalRequestDetails',
    component: ReceiptUserPersonalRequestDetails,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },
  {
    // 영수증 월별 정산 내역(마감된것만)
    path: 'user/settlement-list',
    name: 'ReceiptUserSettlementList',
    component: ReceiptUserSettlementList,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },



  // --------------------- 관리자 영역 ---------------------

  {
    // 영수증 메인화면 신청 내역 확인(처음 진입시 신청 현황을 한눈에 보기 위해)
    path: 'admin/submission',
    name: 'ReceiptAdminSubmission',
    component: ReceiptAdminSubmission,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },
  {
    // 영수증 신청 개인 내역
    path: 'admin/personal-request-details',
    name: 'ReceiptAdminPersonalRequestDetails',
    component: ReceiptAdminPersonalRequestDetails,
    meta: { requiresAuth: !vueTest },
  },
  {
    // 영수증 월별 신청 상세 내역 확인
    path: 'admin/monthly-details',
    name: 'ReceiptAdminMonthlyDetails',
    component: ReceiptAdminMonthlyDetails,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },
  {
    // 영수증 마감
    path: 'admin/closure',
    name: 'ReceiptAdminClosure',
    component: ReceiptAdminClosure,
    meta: { requiresAuth: !vueTest }, // 인증이 필요한 경로
  },


];

export default receiptRoutes;
