<template>
    <div>
      <!-- Main Content Area -->
      <div class="content">
        <h2 class="content-title">실적 관리</h2>
        <div class="d-flex justify-content-between align-items-center">
          <div class="text-muted">Performance Management Status</div>
          <!-- Show button only if user is admin -->
          <button v-if="isAdmin" class="btn btn-primary search-button" @click="openModal">실적 추가</button>
        </div>
  
        <!-- Table View (Visible on larger screens) -->
        <table class="table mt-3" id="dataTable" v-if="!isMobile">
          <thead>
            <tr>
              <th>날짜</th>
              <th>업무 유형</th>
              <th>내용</th>
              <th>성과</th>
              <th>상태</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in paginatedData" :key="item.date">
              <td>{{ item.date }}</td>
              <td>{{ item.workType }}</td>
              <td>{{ item.content }}</td>
              <td>{{ item.performance }}</td>
              <td :class="getStatusClass(item.status)">{{ item.status }}</td>
            </tr>
          </tbody>
        </table>
  
        <!-- Card Layout View (Visible on smaller screens) -->
        <div class="card-layout" v-if="isMobile">
          <div class="card" v-for="item in paginatedData" :key="item.date">
            <div class="card-body">
              <h5 class="card-title">{{ item.date }}</h5>
              <hr />
              <p class="card-text"><strong>업무 유형:</strong> {{ item.workType }}</p>
              <p class="card-text"><strong>내용:</strong> {{ item.content }}</p>
              <p class="card-text"><strong>성과:</strong> {{ item.performance }}</p>
              <p class="card-text">
                <strong>상태:</strong> <span :class="getStatusClass(item.status)">{{ item.status }}</span>
              </p>
            </div>
          </div>
        </div>
  
        <!-- Pagination Buttons -->
        <nav>
          <ul class="pagination justify-content-center" id="pagination">
            <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
              <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
            </li>
          </ul>
        </nav>
      </div>
  
      <!-- Modal for Adding Performance -->
      <div class="modal fade" id="performanceModal" tabindex="-1" role="dialog" aria-labelledby="performanceModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="performanceModalLabel">실적 추가</h5>
              <button type="button" class="close" aria-label="Close" @click="closeModal">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <form @submit.prevent="submitPerformance">
                <div class="form-group">
                  <label for="date">날짜</label>
                  <input type="date" class="form-control" id="date" v-model="formData.date" />
                </div><br>
                <div class="form-group">
                  <label for="workType">업무 유형</label>
                  <select class="form-control" id="workType" v-model="formData.workType">
                    <option>프로젝트</option>
                    <option>회의</option>
                    <option>보고서 작성</option>
                  </select>
                </div><br>
                <div class="form-group">
                  <label for="content">내용</label>
                  <input type="text" class="form-control" id="content" placeholder="내용을 입력해주세요" v-model="formData.content" />
                </div><br>
                <div class="form-group">
                  <label for="performance">성과</label>
                  <input type="text" class="form-control" id="performance" placeholder="성과를 입력해주세요" v-model="formData.performance" />
                </div><br>
                <div class="modal-footer">
                  <button type="button" class="btn btn-secondary" @click="closeModal">Cancel</button>
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, computed, onMounted } from 'vue';
  import * as bootstrap from 'bootstrap';
  
  const data = ref([
    { date: '2024-08-01', workType: '프로젝트', content: '프로젝트 A 진행', performance: '완료', status: '완료' },
    { date: '2024-08-02', workType: '회의', content: '팀 미팅', performance: '진행 중', status: '진행 중' },
    { date: '2024-08-03', workType: '보고서 작성', content: '분기 보고서 작성', performance: '대기 중', status: '대기 중' },
    // Additional data...
  ]);
  
  const currentPage = ref(1);
  const itemsPerPage = ref(5);
  const isMobile = ref(window.innerWidth <= 1150);
  const userRole = ref('admin');
  const isAdmin = computed(() => userRole.value === 'admin');
  
  const formData = ref({
    date: '',
    workType: '프로젝트',
    content: '',
    performance: ''
  });
  
  const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));
  
  const paginatedData = computed(() => {
    const startIndex = (currentPage.value - 1) * itemsPerPage.value;
    return data.value.slice(startIndex, startIndex + itemsPerPage.value);
  });
  
  const goToPage = (page) => {
    currentPage.value = page;
  };
  
  const openModal = () => {
    const modal = document.getElementById('performanceModal');
    const bootstrapModal = new bootstrap.Modal(modal);
    bootstrapModal.show();
  };
  
  const closeModal = () => {
    const modal = document.getElementById('performanceModal');
    const bootstrapModal = bootstrap.Modal.getInstance(modal);
    bootstrapModal.hide();
  };
  
  const submitPerformance = () => {
    console.log('Performance Submitted:', formData.value);
    closeModal();
  };
  
  const updateIsMobile = () => {
    isMobile.value = window.innerWidth <= 1150;
  };
  
  const getStatusClass = (status) => {
    if (status === '진행 중') return 'text-primary'; // Blue
    if (status === '완료') return 'text-success'; // Green
    if (status === '대기 중') return 'text-warning'; // Orange
    return '';
  };
  
  onMounted(() => {
    window.addEventListener('resize', updateIsMobile);
  });
  </script>
  
  <style scoped>
  .content {
    padding: 30px 100px 0 100px;
    margin-left: 220px;
  }
  
  .content-title {
    margin-top: 30px;
    margin-bottom: 60px;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .close {
    font-size: 1.5rem;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .search-button {
    font-size: 0.875rem;
    padding: 0.4rem 0.75rem;
  }
  
  #dataTable tbody tr:hover td {
    background-color: #e7f1ff !important;
  }
  
  @media (max-width: 1150px) {
    .card {
      margin-bottom: 10px;
    }
    .card-title {
      font-size: 1rem;
      margin-bottom: 0px;
    }
    .page-link {
      font-size: 0.75rem;
      padding: 0.3rem 0.6rem;
    }
    .page-item {
      font-size: 0.75rem;
    }
    .pagination {
      margin: 3px 0 40px 0;
    }
    .search-button {
      margin-bottom: 10px;
    }
  }
  
  @media (max-width: 768px) {
    button {
      font-size: 0.75rem;
      padding: 0.3rem 0.6rem;
    }
    body {
      padding-top: 0px;
    }
    h2 {
      font-size: 1.25rem;
    }
    .mb-3 {
      margin-bottom: 0.25rem !important;
    }
    .search-button {
      font-size: 0.63rem;
      padding: 0.3rem 0.6rem;
    }
    .content {
      margin-left: 0;
      padding: 20px;
      font-size: 0.875rem;
    }
    .content-title {
      font-size: 1.25rem;
      margin-bottom: 70px;
    }
    .align-items-center {
      margin-bottom: 10px;
    }
    .card {
      margin-bottom: 10px;
    }
    .card-title {
      font-size: 0.8rem;
      margin-bottom: 0px;
    }
    .text-muted, .card-text {
      font-size: 0.75rem;
      margin-bottom: 7px;
    }
    .page-link {
      font-size: 0.75rem;
      padding: 0.3rem 0.6rem;
    }
    .page-item {
      font-size: 0.75rem;
    }
    .pagination {
      margin: 3px 0 40px 0;
    }
  }
  </style>
  